.App {
  text-align: center;
  padding-top: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;  
  justify-content: center;
  min-height: 100vh;
  padding: 20px;
  text-align: center;
  font-family: 'Poppins', sans-serif;
}

.upload-btn {
  display: inline-block;
  margin: 30px auto; 
  padding: 20px 65px;
  color: white;
  font-weight: 600;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  transition: background 0.3s ease;
  font-family: 'Poppins', sans-serif; 
}



.image-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 8px;
}


/* Şəkil üslubu */
.image-fixed {
  width: 100%;
  aspect-ratio: 1 / 1; /* width = height */
  object-fit: cover;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

/* Planşet üçün (>= 768px) */
@media (min-width: 768px) {
  .image-grid {
    grid-template-columns: repeat(5, 1fr);
  }
}

/* Kompüter üçün (>= 1200px) */
@media (min-width: 1200px) {
  .image-grid {
    grid-template-columns: repeat(8, 1fr);
  }
  .gallery{
    padding-left:20px;
    padding-right: 20px;
  }
}


.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.loader {
  border: 6px solid #f3f3f3;
  border-top: 6px solid #2575fc;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 1s linear infinite;
}

.password-input {
  padding: 10px;
  margin-bottom: 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 16px;
  width: 250px;
}

.admin {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  gap: 15px;
  text-align: center;
}


.upload-btn:hover {
  transform: scale(1.05);
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.loader {
  border: 6px solid #f3f3f3;
  border-top: 6px solid #4e54c8;
  border-radius: 50%;
  width: 50px;
  height: 50px;
