  }
}

.custom-alert {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}




.cancel-btn:hover {
  background-color: #e60000;
}

.labelGallery {
  margin-bottom: 50px;
  font-size: 50px;
    font-family: "Great Vibes", cursive;
}

/* Modal background */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0,0,0,0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  cursor: pointer;
}

/* Modal content box */
.modal-content {
  position: relative;
  max-width: 90vw;
  max-height: 90vh;
  cursor: default;
}

/* Large image inside modal */
.modal-content img {
  max-width: 100%;
  max-height: 90vh; /* limit height to 90% of viewport */
  height: auto;     /* keep aspect ratio */
  border-radius: 12px;
  display: block;
}

/* Close button */
.modal-close {
  position: absolute;
  top: 8px;
  right: 8px;
  background: rgba(255,255,255,0.8);
  border: none;
  font-size: 24px;
  line-height: 1;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  cursor: pointer;
  font-weight: bold;
  color: #333;
  transition: background 0.3s ease;
}

.modal-close:hover {
  background: rgba(255,255,255,1);
}


@keyframes fadeInOut {
  0% { opacity: 0; transform: translate(-50%, -60%); }
  10% { opacity: 1; transform: translate(-50%, -50%); }
  90% { opacity: 1; transform: translate(-50%, -50%); }
  100% { opacity: 0; transform: translate(-50%, -40%); }
}


@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.download-btn {
  padding: 10px 20px;
  background: linear-gradient(135deg, #4b1e1e, #6b2e1a, #8e6a41);
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  user-select: none;
}

.download-btn:disabled {
  background: #999;
  cursor: not-allowed;
}

/* Wrapper around image + checkbox */
.image-wrapper {
  position: relative;
  display: inline-block;
}

/* Checkbox at top-left corner */
.image-checkbox {
  position: absolute;
  top: 6px;
  left: 6px;
  z-index: 10;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

/* Existing modal & image styles should stay the same */

.top-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap:20px;
  margin-bottom: 30px;
}

.top-controls .left {
  display: flex;
  align-items: center;
  gap: 10px;
}

.top-controls .right {
  display: flex;
  align-items: center;
  gap: 10px;
}

.gallery{
  text-align: center;
  padding: 15px;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  text-align: center;
  font-family: 'Poppins', sans-serif;
}
.select-all {
