}

.delete-btn {
  padding: 10px 20px;
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  user-select: none;
  background: #e53935;
}

.delete-btn:disabled {
  background: #999;
  cursor: not-allowed;
}

